<template>
  <el-dialog
    :title="showTitle"
    :visible.sync="dialogVisible"
    width="520px"
    :before-close="closeDiglog"
  >
    <el-form
      :model="moduleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="proLabel" disabled />
      </el-form-item>
      <el-form-item label="" v-if="parentObj.label !== ''">
        <el-input v-model="parentObj.label" disabled />
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input v-model="moduleForm.name" />
      </el-form-item>
      <el-form-item style="text-align: right">
        <el-button type="primary" @click="submitForm('ruleForm')"
          > </el-button
        >
        <el-button @click="closeDiglog"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import ModuleApi from "../../request/module";
export default {
  props: ["rootId", "pid", "proLabel", "parentObj"],
  name: "projectDialog",
  data() {
    return {
      showTitle: "",
      dialogVisible: true,
      moduleForm: {
        name: "",
        project_id: 0,
        parent_id: 0,
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      fileList: [],
    };
  },
  mounted() {
    this.moduleForm.project_id = this.pid;
    if (this.rootId === true) {
      this.showTitle = "";
    } else {
      this.showTitle = "";
      this.moduleForm.parent_id = this.parentObj.id;
    }
  },
  methods: {
    closeDiglog() {
      this.$emit("cancel", {});
    },

    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          ModuleApi.createModules(this.moduleForm).then((resp) => {
            if (resp.success === true) {
              this.closeDiglog();
              this.$message.success("");
            } else {
              this.$message.error(resp.error.msg);
            }
          });
        }
      });
    },
  },
};
</script>
