<template>
  <el-dialog
    :title="showTitle"
    :visible.sync="dialogVisible"
    width="520px"
    :before-close="closeDiglog"
  >
    <el-form
      :model="projectForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="projectForm.name" />
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input type="textarea" v-model="projectForm.describe" />
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-upload
          action="#"
          :before-upload="beforeUpload"
          :file-list="fileList"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          list-type="picture-card"
        >
          <i class="el-icon-plus" />
        </el-upload>
        <el-dialog :visible.sync="imageVisible">
          <img width="100%" :src="imageUrl" alt="" />
        </el-dialog>
      </el-form-item>
      <el-form-item style="text-align: right">
        <el-button type="primary" @click="submitForm('ruleForm')"
          > </el-button
        >
        <el-button @click="closeDiglog"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
import ProjectApi from "../../request/project";
export default {
  props: ["title", "pid"],
  name: "projectDialog",
  data() {
    return {
      showTitle: "",
      imageUrl: "",
      imageVisible: false,
      dialogVisible: true,
      projectForm: {
        name: "",
        describe: "",
        image: "",
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      fileList: [],
    };
  },
  mounted() {
    if (this.title === "create") {
      this.showTitle = "";
    } else if (this.title === "edit") {
      this.showTitle = "";
      this.initProject();
    }
  },
  methods: {
    // 
    handleRemove(file) {
      console.log("", file);
    },
    // 
    handlePreview(file, fileList) {
      console.log(fileList);
      this.imageUrl = file.url;
      this.imageVisible = true;
    },
    closeDiglog() {
      this.$emit("cancel", {});
    },
    //
    async initProject() {
      const resp = await ProjectApi.getProject(this.pid);
      if (resp.success === true) {
        this.projectForm = resp.item;
        this.fileList.push({
          name: resp.item.image,
          url: "static/images/" + resp.item.image,
        });
        this.$message.success("");
      } else {
        this.$message.error("");
      }
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.title === "create") {
            ProjectApi.createProject(this.projectForm).then((resp) => {
              if (resp.success === true) {
                this.closeDiglog();
                this.$message.success("");
              } else {
                this.$message.error(resp.error.message);
              }
            });
          } else if (this.title === "edit") {
            ProjectApi.updateProject(this.pid, this.projectForm).then(
              (resp) => {
                if (resp.success === true) {
                  this.closeDiglog();
                  this.$message.success("");
                } else {
                  this.$message.error(resp.error.message);
                }
              }
            );
          }
        } else {
          return false;
        }
      });
    },
    beforeUpload(file) {
      console.log(file);
      let fd = new FormData();
      fd.append("file", file);

      ProjectApi.uploadImage(fd).then((resp) => {
        if (resp.data.success === true) {
          this.projectForm.image = resp.data.item.name;
          const imagePath = "/static/images/" + resp.data.item.name;
          console.log("imagePath", imagePath);
          this.fileList.push({
            name: file.name,
            url: imagePath,
          });
          this.$message.success("");
        } else {
          console.log("", resp);
          this.$message.error(resp.error.message);
        }
      });
      return true;
    },
  },
};
</script>
