import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login.vue";
import Navigation from "../views/Navigation.vue";
import Project from "../components/project/project_list.vue";
import caseModule from "../components/case/caseModule.vue";
import tasks_list from "../components/tasks/tasks_list.vue";
import report_list from "../components/report/report_list.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/login",
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/main",
    name: "Navigation",
    component: Navigation,
    children: [
      {
        //  /main/home 
        // project  Navigation  <router-view> 
        path: "home",
        component: Home,
      },
      {
        path: "project",
        component: Project,
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        // component: () =>
        //   import(/* webpackChunkName: "about" */ "../views/About.vue"),
      },
      {
        path: "case",
        component: caseModule,
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        // component: () =>
        //   import(/* webpackChunkName: "about" */ "../views/About.vue"),
      },
      {
        path: "tasks",
        component: tasks_list,
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        // component: () =>
        //   import(/* webpackChunkName: "about" */ "../views/About.vue"),
      },
      {
        path: "report",
        component: report_list,
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        // component: () =>
        //   import(/* webpackChunkName: "about" */ "../views/About.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});

// 
router.beforeEach((to, from, next) => {
  if (to.path === "/login") {
    // login
    next();
  } else {
    // 
    if (sessionStorage.token) {
      // 
      next();
    } else {
      next({ path: "/login" }); // login
    }
  }
});

export default router;
